div.component-form {

    @extend .text-center ;

    > form {
        @extend .mx-auto ;
        &.subscribe { max-width: 20rem ; }
        &.contact { max-width: $para-max-width ; }      
        > div {
            @extend .form-group ;
            input, textarea { @extend .form-control ; }
        }
        button { @extend .btn, .btn-green ; }
    }

}
