//----------------------------------------
// Header + Footer
// xs viewport. 1+x rows, 1 logo link and x for nav items
// sm viewport. 2 rows, 1 for logo link and 1 for nav items
// md viewport. 1 row for both logo link and nav items

div.component-nav-bar {
    ul {
        @extend .row, .list-unstyled, .text-center ;
        @extend .mt-150s, .mt-md-0 ;
        > li {
            @extend .col-12, .col-sm ;
            &:not(:first-child) { @extend .mt-100s, .mt-sm-0 ; }
        }
    }
    a { @extend .p-25s ; }
    .logo-link > a {
        @include font-size($lead-font-size) ;
        @extend .font-weight-bold ;
    }
}

div.nav-bar-toggle {
    @extend .navbar, .navbar-expand-md, .navbar-light ;
    ul { @extend .ml-auto ; }
}

div.nav-bar-normal {
    @extend .d-flex, .align-items-center, .flex-column, .flex-md-row ;
    @extend .justify-content-md-between ;
}
