//----------------------------------------
// Colors

// source: leap.mediumra.re/home-saas-trend.html
$orange: #ff623f; // link/button/bg for light text
$green: #225560; // bg for light text
$gray-17: #171219; // bg for light text
$gray-f2: #f2f5f5; // bg for dark text
$light: $gray-f2;
$dark: $gray-17;

// source: bootstrap/_variables.scss
$gray-f8: #f8f9fa; // light para text for dark bg
$gray-49: #495057; // dark para text for light bg
$gray-21: #212529; // dark heading text for light bg

$gray-ad: #adb5bd; // dark border for light bg
$white-transparent: rgba(255, 255, 255, .2); // border for header

// used to generate colors for .bg-, .border-, .btn-, .text-,
$theme-colors: (
    'orange': $orange,
    'green': $green,
    'gray-17': $gray-17,
    'gray-21': $gray-21,
    'gray-49': $gray-49,
    'gray-ad': $gray-ad,
    'gray-f2': $gray-f2,
    'gray-f8': $gray-f8,
    'white-transparent': $white-transparent,
);

$yiq-contrasted-threshold: 195;

//----------------------------------------
// RFS

$enable-responsive-font-sizes: true;
$rfs-base-font-size: 1rem;
$rfs-factor: 3;

//----------------------------------------
// Spacing

$spacer: 1rem;

$spacers: (
    '15s': $spacer * 0.15,
    '25s': $spacer * 0.25,
    '50s': $spacer * 0.5,
    '75s': $spacer * 0.75,
    '100s': $spacer,
    '150s': $spacer * 1.5,
    '200s': $spacer * 2,
    '250s': $spacer * 2.5,
    '300s': $spacer * 3,
    '400s': $spacer * 4,
    '500s': $spacer * 5,
);

//----------------------------------------
// Typography

// body font weights: 400, 600
// heading font weights: 600
// cross-check with web font embed

$font-family-base: 'IBM Plex Sans', sans-serif;
$font-size-base: 1rem; // body font size
$font-weight-bold: 600;

$headings-font-family: 'Open Sans', sans-serif;
$h1-font-size: $font-size-base * 2;
$h2-font-size: $font-size-base * 1.5;
$h3-font-size: $font-size-base;
$headings-font-weight: 600;
$headings-line-height: 1.3;
$headings-margin-bottom: 0;

$display1-size: $font-size-base * 3;
$display2-size: $display1-size * 0.8; // 2.4
$display3-size: $display2-size * 0.8; // 1.92
$display4-size: $font-size-base * 1.4;
$display1-weight: 700;
$display2-weight: $display1-weight;
$display3-weight: 600;
$display4-weight: $display3-weight;

$lead-font-size: $font-size-base * 1.3;
$lead-font-weight: 400;

$paragraph-margin-bottom: 0;

$list-inline-padding: spacer('25s');

//--------------------------------------------------------------------------------

//----------------------------------------
// Body

$body-color: theme-color('gray-49');

//----------------------------------------
// Borders

$border-color: theme-color('gray-f8');

//----------------------------------------
// Buttons

$btn-font-size: $font-size-base * 1.25;
$btn-font-weight: $font-weight-bold;
$btn-line-height: 1;
$btn-padding-y: spacer('100s');
$btn-padding-x: spacer('200s');

//----------------------------------------
// Links

$link-color: theme-color('orange');
$link-hover-color: $link-color;

//----------------------------------------
// Navbar

$navbar-padding-y: 0;
$navbar-padding-x: 0;

$navbar-toggler-padding-y: 0;
$navbar-toggler-padding-x: 0;

$navbar-light-color: #fff;
$navbar-light-toggler-icon-bg: str-replace(url("data:image/svg+xml,%3csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3e%3cpath stroke='#{$navbar-light-color}' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e"), "#", "%23");

//--------------------------------------------------------------------------------

@import '../../_vendor/bootstrap/4.3.1/stylesheets/variables';
