$para-max-width: 35rem ;

section {

    @extend .py-400s ;

    p { @extend .mx-auto ; max-width: $para-max-width ; }
    
    a { @extend .text-decoration-none ; }
    .button > a { @extend .btn, .btn-green ; }
    .link > a { @extend .font-weight-bold ; }
    
    &.fg-light {
        h1, h2 { @extend .text-white ; }
        p { @extend .text-gray-f8 ; }
        a { @extend .text-white ; }
    }
    &.fg-dark {
        h1, h2 { @extend .text-gray-21 ; }
    }

}
