div.component-list {

    h1, .link { @extend .mx-auto ; max-width: $para-max-width ; }
    
    h1 + p { @extend .mt-100s ; }
    p + .link { @extend .mt-75s ; }
    
    h1 { @extend .display-4 ; }

    ul {
        @extend .row, .list-unstyled ;
        > li {
            &.rows {
                &:not(:first-child) { @extend .mt-250s ; }
            }
            &.columns-lg {
                &:not(:first-child) { @extend .mt-lg-0 ; }
            }
            &.frames {
                > div { @extend .h-100, .p-200s, .border, .border-gray-ad, .rounded ; }
            }
            &.cards {
                > div { @extend .bg-white ; }
            }
            &.quotes {
                h1 { @extend .text-green ; }
            }
        }
    }
}
